import { Grid } from "@material-ui/core";
import { useState } from "react";
import BackButton from "../Common/BackButton";
import DatePickersTrain from "./datePickersTrain";
const Train = () => {
  const [Respuesta, setRespuesta] = useState(null);

  const res = () => {
    if (Respuesta != null)
      return (
        <>
          <p><strong>Entrenado:</strong> {Respuesta.trained}</p>
          <p><strong>Colección:</strong> {Respuesta.collection_name}</p>
        </>
      );
  };
  return (
    <Grid container direction="column" alignItems="center" justifyContent="center">
      <h1>Entrenamiento del algoritmo</h1>
      <BackButton/>
      <DatePickersTrain setDatos={setRespuesta} />

      {res()}
    </Grid>
  );
};

export default Train;
